const TabsRenderer = ({ tabs, activeTabIndex, className, customCss }) => {
  const [activeTab, setActiveTab] = React.useState(activeTabIndex);
  const uniqueClass = `tab-${Math.random().toString(36).substr(2, 9)}`;

  React.useEffect(() => {
    setActiveTab(activeTabIndex);
  }, [activeTabIndex]);

  return (
    <div className={`puck-tabs ${className || ""}`}>

      {customCss && <style>{`.${uniqueClass} { ${customCss} }`}</style>}

      <div
        style={{
          display: "flex",
          borderBottom: "1px solid #e5e7eb",
          marginBottom: "16px",
          gap: "24px",
        }}
      >
        {tabs.map((tab, index) => {
          const isActive = index === activeTab;

          return (
            <button
              key={index}
              type="button"
              onClick={() => setActiveTab(index)}
              style={{
                background: "none",
                border: "none",
                padding: "10px 4px",
                fontSize: "14px",
                color: isActive ? "#111827" : "#6b7280",
                fontWeight: isActive ? "600" : "400",
                cursor: "pointer",
                position: "relative",
              }}
            >
              {tab.icon && <span style={{ marginRight: "6px" }}>{tab.icon}</span>}
              {tab.label}

              {isActive && (
                <span
                  style={{
                    position: "absolute",
                    bottom: "-1px",
                    left: 0,
                    width: "100%",
                    height: "2px",
                    background: "#111827",
                  }}
                />
              )}
            </button>
          );
        })}
      </div>

      {/* ======= TAB CONTENT WITH DROPZONES ======= */}
      <div>
        {tabs.map((tab, index) => {
          const isActive = index === activeTab;

          return (
            <div
              key={index}
              style={{
                display: isActive ? "block" : "none",
                minHeight: "150px",
                padding: "8px 0",
              }}
            >
              {tab.defaultContent && (
                <div style={{ marginBottom: "10px", color: "#555" }}>
                  {tab.defaultContent}
                </div>
              )}

              <DropZone zone={`tab-${index}`} />
            </div>
          );
        })}
      </div>
    </div>
  );
};

